<template>
  <div>

    <el-dialog
      title="文章内容"
      :visible.sync="dialogForm.dialogVisible"
      width="50%"
      :before-close="handleClose"
      :close-on-click-modal ="false">
      <div slot="title" style="text-align: left;">
          <div>文章内容</div>
          <hr style="height:1px;border:none;border-top:1px solid #E0E0E0;" />
      </div>       
      <div style="height:700px">
        <editor @onEditorChange="onEditorChange" :value="dialogForm.ArtContent" style="height:640px"></editor>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveArtContent" size="small">保 存</el-button>
        <el-button @click="dialogForm.dialogVisible = false" size="small">关 闭</el-button>
      </span>
    </el-dialog>

    <!-- 页面主体 -->
    <div class="boxmain">

      <!--检索条件-->
      <div>
        <div>
          <el-input v-model="formData.ArtTitle" placeholder="标题 and" size="mini" style="width:150px"></el-input>
          <el-input v-model="formData.Tag" placeholder="标签 and" size="mini" style="width:150px;margin-left:10px"></el-input>      
          <el-button type="info" @click="search()" size="mini" style="margin-left:10px">检索</el-button>   
        </div>

        <div>
          <el-button type="info" @click="addRow()" size="mini" style="margin-top:10px">添加</el-button>   
        </div>
        
      </div>
    
      <!--统计信息-->
      <div style="margin-top:0px;">
        <div style="display:table;width:100%;border-collapse:separate; border-spacing:0px 10px;">
          <div style="display:table-row">
            <div style="display:table-cell">
                <span>共：{{total}}</span>
            </div>
          </div>
        </div>
      </div>

      <!--列表信息-->
      <div>

        <!--检索结果-->
        <el-table v-loading="loading"
            :data="viewData.gridData"
            style="font-size: 13px;width: 100%;margin-bottom: 20px;"
            :header-cell-style="({row, rowIndex})=>{return 'background-color:#F5F5F5;text-align:center;'}"
            :cell-style="({row, column, rowIndex, columnIndex})=>{return 'padding:5px 0px'}"
            @selection-change="handleSelectionChange">
            <el-table-column
              type="index"
              :index="indexMethod"
              width="40">
            </el-table-column>            
            <!-- <el-table-column type="selection"
                          label=""
                          width="30">
            </el-table-column> -->
            <el-table-column label="标题" prop="ArtTitle" width="500" align="left">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.ArtTitle" placeholder="标题" size='mini'></el-input>
                </span>
                <span v-else>
                  <el-link icon="el-icon-edit"  type="primary" @click="viewDialog(scope.row)" style="margin-left:5px"></el-link>
                  <span v-if="scope.row.ArtContent">*</span>
                  {{scope.row.ArtTitle}}
                </span>
              </template>               
            </el-table-column> 
            <el-table-column label="标签" prop="Tag" width="200" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.Tag" placeholder="标签" size='mini'></el-input>                 
                </span>
                <span v-else>{{scope.row.Tag}}</span>
              </template>                
            </el-table-column>              
            <el-table-column label="" prop="" align="center">>
               <template slot-scope="scope">
                <el-button v-if="rowForm.type == '0'" type="info" @click="editRow(scope.row)" size="mini">修改</el-button>
                <el-button v-if="rowForm.type == '0'" type="info" @click="delRow(scope.row)" size="mini">删除</el-button>
                <el-button v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id" type="info" @click="saveRow(scope.row)" size="mini">保存</el-button>
                <el-button v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id" type="info" @click="cancelRow()" size="mini">取消</el-button>
              </template>
            </el-table-column>                                                                                                   
                                  
        </el-table>

      </div>

    <!--页码-->
    <div style='margin-top:20px;text-align: center;'>
        <el-pagination background
          layout='total,sizes, prev, pager, next, jumper'
          :total='total'
          :page-sizes="[20,50,100,200]"
          :page-size='pagination.perPageNum'
          :current-page='pagination.curPage'
          @current-change='handleCurrentChange'
          @size-change="handleSizeChange">
        </el-pagination>
    </div>    
    
    </div>

  </div>

</template>
<script>
import editor from '@/components/QuillEditor.vue';
export default {
  name: 'stockart',
  data() {
    return {
      fn:'stockart',      
      posturl:'/StockArt',
      loading: false,     

      viewData:{
        gridData:[],
        count: 0
      },

      formData:{
        multipleSelection:[],
        ArtTitle:'',
        Tag:""
      },

      pagination: {
          perPageNum:20,//默认
          curPage: 1 //页码
      },

      dialogForm:{
        dialogVisible: false,
        Id:"",
        ArtContent:""
      },

      rowForm:{
        type:'0', //0:显示模式 1:编辑模式
        formData:{
          Id:"",
          ArtTitle:'',
          ArtContent:'', 
          Tag:''
        }
        
      }          
     
    }
  },
  /* 这个是vue的钩子函数，当new Vue()实例创建完毕后执行的函数 */
  created () {   

    var _this = this;
    _this.search();

  },
  mounted(){
    // 绑定监听事件
    window.addEventListener('keydown', this.keyDown);        
  },  
  updated(){
  },
  components: { 
    editor
  },
  //计算属性
  computed: {
    //检索结果记录数T
    total:function() {
        var _this = this;
        return Number(_this.viewData.count);
    },
    UserId:function(){
      var userid = this.$store.state.UserInfo.UserId?this.$store.state.UserInfo.UserId:'';
      return userid;
    }    
  },
  destroyed() {
    // 销毁事件
    window.removeEventListener('keydown', this.keyDown, false);
  },    
  //方法
  methods: {

    //检索
    search() {

      var _this = this;

      try{
        
          _this.loading = true;

        var data = {
            CurPage: _this.pagination.curPage,
            PerPageNum: _this.pagination.perPageNum,
            ArtTitle:_this.formData.ArtTitle,
            Tag:_this.formData.Tag,
            UserId:_this.UserId
          }

        _this.$http.post(_this.posturl + "/search",data)
        .then(function (response) {

          if (response.data.Status == "false") {

            _this.$message.error(response.data.ErrMsg);

          } else {

            // console.log(response);

            var result = response.data.ResultData;

            _this.viewData.gridData = result.gridData;
            _this.viewData.count = result.count;

          }   

          _this.loading = false;
          
        })
        .catch(function (error) {

          console.log(error);

        }); 

      }catch(err){

        //_this.$message.error(err.message);
        alert(err.message);

      } 

    },

    //换页
    handleCurrentChange:function(pageNo) {
        var _this = this;
        _this.pagination.curPage = pageNo;
        _this.search();
    },    

    //更改size
    handleSizeChange:function(val) {
        var _this = this;
        _this.pagination.perPageNum = val;
        _this.search();
    },  
    
    //选择列表
    handleSelectionChange:function(val) {
        var _this = this;
        _this.formData.multipleSelection = val;
    },    

    //显示对话框
    viewDialog(row){
      var _this = this;

      _this.dialogForm.dialogVisible = true;

      _this.dialogForm.Id = row.Id;
      _this.dialogForm.ArtContent = row.ArtContent;

    },

    //关闭对话框
    handleClose(done) {
      
      // this.$confirm('确认关闭？')
      //   .then(() => {
      //     done();
      //   })
      //   .catch(() => {});

      done();
      
    },

    onEditorChange(html) {

      var _this = this;

      _this.dialogForm.ArtContent = html;

    },

    //保存文章内容
    saveArtContent(){

      var _this = this;

      try{

          _this.loading = true;

          var data = {
              Id: _this.dialogForm.Id,
              ArtContent: _this.dialogForm.ArtContent
            }

          _this.$http.post(_this.posturl + "/saveartcontent",data)
          .then(function (response) {

            if (response.data.Status == "false") {

              _this.$message.error(response.data.ErrMsg);

            } else {

              _this.$message.success("保存成功!");
              _this.search();
              //_this.dialogForm.dialogVisible = false;

            }   

            _this.loading = false;
            
          })
          .catch(function (error) {

            console.log(error);

          }); 

        }catch(err){

          //_this.$message.error(err.message);
          alert(err.message);

        } 

    },

    //表格索引
    indexMethod(index) {
        var _this = this;
        return (_this.pagination.curPage - 1) * _this.pagination.perPageNum + index + 1;
    },

    //编辑行
    editRow(row){
      
      var _this = this;

      _this.rowForm.type = 1;

      _this.rowForm.formData.Id = row.Id;
      _this.rowForm.formData.ArtTitle = row.ArtTitle;
      _this.rowForm.formData.Tag = row.Tag;
  
    },    
    //保存行
    saveRow(){

      var _this = this;

      try{
        
          _this.loading = true;

          var action = '/saverow';

          var data = _this.rowForm.formData;

          // data.UserId = _this.UserId;

          _this.$http.post(_this.posturl + action,data)
          .then(function (response) {

            if (response.data.Status == "false") {

              _this.$message.error(response.data.ErrMsg);

            } else {

              _this.rowForm.type = 0;
              _this.search();

            }   

            _this.loading = false;
            
          })
          .catch(function (error) {

            console.log(error);

          }); 

        }catch(err){

          //_this.$message.error(err.message);
          alert(err.message);

        } 

    },

    //添加行
    addRow(){

      var _this = this;

      if(!_this.viewData.gridData)
      {
        _this.viewData.gridData = [];
      } 

      _this.viewData.gridData.unshift({
      
          Id:'',
          ArtTitle:'',
          Tag:''

      });
      
      _this.rowForm.type = 1;
      _this.rowForm.formData.Id = '';
      _this.rowForm.formData.UserId = _this.UserId;

    },

    //编辑行-取消
    cancelRow(){
      var _this = this;
      _this.rowForm.type = 0;

      if(_this.rowForm.formData.Id == ''){
        _this.viewData.gridData.shift();
      }

    },

    //ctrl+s 保存
    keyDown(e) {

      if(!this.dialogForm.dialogVisible){
        return;
      }

      //ctrl+s 保存
      if (e.keyCode === 83 && e.ctrlKey) {

        this.saveArtContent();

        event.preventDefault();

        event.returnValue = false;

        return false;          
      }

    },
    
    //删除
    async delRow(row){

      var _this = this;

      try{

        var isDeleted = false;
        await _this.$confirm('确认删除？')
          .then(() => {
            isDeleted = true;
          })
          .catch(() => {});

          if(!isDeleted){
            return;
          }

          _this.loading = true;

          var data = {
              Id: row.Id,
            }

          _this.$http.post(_this.posturl + "/delete",data)
          .then(function (response) {

            if (response.data.Status == "false") {

              _this.$message.error(response.data.ErrMsg);

            } else {

              _this.$message.success("删除成功!");
              _this.search();
              //_this.dialogForm.dialogVisible = false;

            }   

            _this.loading = false;
            
          })
          .catch(function (error) {

            console.log(error);

          }); 

        }catch(err){

          //_this.$message.error(err.message);
          alert(err.message);

        } 

    },    

  }
}
</script>

<style scoped>

  .boxmain {
    width:100%;
    margin: 0px auto;
    text-align: left;
    font-size: 15px;
  }

</style>
