<template>
  <div>
    <div style="display:none;">
      <el-upload  
      class="upload-demo"
      :action="uploadForm.uploadUrl"
      :data="uploadForm.upLoadData"
      :file-list="uploadForm.fileList"
      :on-success="handleUploadSuccess"
      :before-upload="beforeUploadImage"
      multiple>

        <div ref="elupload">

          <el-button size="small" type="primary">点击上传</el-button>

        </div>

      </el-upload>     
    </div>

    <!-- <div class="editor"></div> -->
    <!-- <quill-editor ref="myTextEditor"
                  v-model="content"
                  :config="options"
                  @blur="onEditorBlur($event)"
                  @focus="onEditorFocus($event)"
                  @ready="onEditorReady($event)">
    </quill-editor>     -->
    <!-- <quill-editor ref="myTextEditor"
                  v-model="content"
                  :config="options"
                  @ready="onEditorReady($event)">
    </quill-editor>       -->

    <quill-editor ref="myTextEditor"
                  style="height:100%"
                  v-model="content"
                  :options="options"
                  @change="onEditorChange($event)">
    </quill-editor>      


    <!-- <quill-editor ref="myTextEditor"
      v-model="content"
      :options="options">
    </quill-editor> -->

  </div>
</template>

<script>
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
// import Quill from 'quill'
import { quillEditor,Quill } from 'vue-quill-editor'
import ImageResize from 'quill-image-resize-module' //调节图片大小
import '../assets/js/image-paste.js'
Quill.register('modules/imageResize', ImageResize)  //添加
export default {
  name: 'editor',
  props: {
    //value: Object
    // content:{String}
    value:String
  },
  watch:{
    value(newVal) {
      //console.log(newVal + oldVal);
      this.content = newVal;
    }
  },
  data() {
    return {
      quill:null,
      content:this.value,
      options: {
        // theme: 'snow',
        // modules: {

        //   imageResize: {
        //     displayStyles: {
        //       backgroundColor: 'black',
        //       border: 'none',
        //       color: 'white'
        //     },
        //     modules: ['Resize', 'DisplaySize', 'Toolbar']
        //   },               
        //   toolbar: {
        //     container:[
        //       ['bold', 'italic', 'underline', 'strike'],
        //       ['blockquote', 'code-block'],
        //       [{ 'header': 1 }, { 'header': 2 }],
        //       [{ 'list': 'ordered' }, { 'list': 'bullet' }],
        //       [{ 'script': 'sub' }, { 'script': 'super' }],
        //       [{ 'indent': '-1' }, { 'indent': '+1' }],
        //       [{ 'direction': 'rtl' }],
        //       [{ 'size': ['small', false, 'large', 'huge'] }],
        //       [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
        //       [{ 'color': [] }, { 'background': [] }],
        //       [{ 'font': [] }],
        //       [{ 'align': [] }],
        //       ['clean'],
        //       ['link', 'image', 'video']
        //     ],  
        //     handlers: {
        //       //image: function(value) {
        //       image: function(value) {
        //         // debugger
        //         // vm.$refs.aUpload.click() //自定义图片上传回调
        //         console.log("imageClick=" + value);
        //         //vm.dialogUpload.dialogVisible = true;
        //         //document.getElementById("elupload").click();
                
        //       }
        //     },
        //     placeholder: 'Insert text here ...'
        //   },
          
        // }
      },

      uploadForm: {
          dialogVisible: false,
          dialogTitle: '上传图片',
          uploadType: 'image', //图片：'image' 视频：'video'
          uploadUrl: '/Upload/upload',
          fileUrl:'', //文档Url
          upLoadData: {},
          fileList: [],
          fileName:'',    //文件名
          showProgress: false,//进度条的显示
          progress: 0 //进度条数据
      }
      
    }
  },
  created() {
    var _this = this;

    const options = {
        theme: 'snow',
        modules: {
          clipboard: { matchVisual: false },//解决自动生成<p><br><p>问题
          imageResize: {
            displayStyles: {
              backgroundColor: 'black',
              border: 'none',
              color: 'white'
            },
            modules: ['Resize', 'DisplaySize', 'Toolbar']
          },        
          imagePaste: {
            addImageBlob: function (blob,callback) {
              var formData = new FormData()
              formData.append('image', blob)

              // console.log(formData);
              //console.log(blob);

              // your upload function, get the uploaded image url, add then
              _this.$http.post(_this.uploadForm.uploadUrl, formData, {headers: { "Content-Type": "multipart/form-data" }})
              .then(res => {
                //this.$success("上传图片成功!");
                // this.fileList = [0,0,0];
                // this.uploadFile =[[],[],[]];

                //callback(imageUrl)
                //console.log(res);
                //callback("http://image.rlf99.com/image/timg.jpg");

                callback(res.data.ResultData.FileUrl);
              })
              .catch(e => {
                console.log(e);
              });

              
            } 
          },
          toolbar: {
            container:[
              ['bold', 'italic', 'underline', 'strike'],
              ['blockquote', 'code-block'],
              [{ 'header': 1 }, { 'header': 2 }],
              [{ 'list': 'ordered' }, { 'list': 'bullet' }],
              [{ 'script': 'sub' }, { 'script': 'super' }],
              [{ 'indent': '-1' }, { 'indent': '+1' }],
              [{ 'direction': 'rtl' }],
              [{ 'size': ['small', false, 'large', 'huge'] }],
              [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
              [{ 'color': [] }, { 'background': [] }],
              [{ 'font': [] }],
              [{ 'align': [] }],
              ['clean'],
              ['link', 'image', 'video']
            ],  
            handlers: {
              //image: function(value) {
              image: function(value) {
                // debugger
                // vm.$refs.aUpload.click() //自定义图片上传回调
                console.log("imageClick=" + value);
                //_this.dialogUpload.dialogVisible = true;
                //document.getElementById("elupload").click();
                _this.$refs.elupload.click();
              },
              video: function(value) {
                // debugger
                // vm.$refs.aUpload.click() //自定义图片上传回调
                console.log("videoClick=" + value);
                //_this.dialogUpload.dialogVisible = true;
                //document.getElementById("elupload").click();
                _this.$refs.elupload.click();
              }              
            },
            placeholder: 'Insert text here ...'
          },
          
        }
      }

    _this.options = options;

    //console.log("value=" + this.value);
    
  },  
  mounted() {
    // let dom = this.$el.querySelector('.editor');
    // this.quill = new Quill(dom, this.options);
    // this.quill.setContents(this.value);

    // this.quill.on('text-change', () => {

    //   this.$emit('input', this.quill.getContents());
      
    //   console.log(this.quill.container.firstChild.innerHTML);

    // });


  },
  components: {
     quillEditor
  },
  methods: {
    onEditorBlur(editor) {
      console.log('editor blur!', editor)
    },
    onEditorFocus(editor) {
      console.log('editor focus!', editor)
    },
    onEditorReady(editor) {
      console.log('editor ready!', editor)
    },
    //onEditorChange({ editor, html, text }) {
    onEditorChange({ html }) {
      //console.log('editor change!', editor, html, text)
      //this.content = html
      //console.log('editor change!',  html)
      this.$emit('onEditorChange', html);
    },

    //上传前
    beforeUploadImage:function(file){

        var _this = this;
        // alert(file.name);

        //上传附加数据
        _this.uploadForm.upLoadData = {
            // Action: "Upload",
            // FnName: _this.fnName,
            // OperatorName: getCookie("UserIDFileCheck"),
            UploadType: "image"
        }

        if (_this.uploadForm.uploadType == 'video') {
            if (['video/mp4', 'video/ogg', 'video/flv', 'video/avi', 'video/wmv', 'video/rmvb'].indexOf(file.type) == -1) {
                _this.$message.error('请上传正确的视频格式！');
                return false;
            }

            const isLt30M = file.size / 1024 / 1024 < 10;
            if (!isLt30M) {
                _this.$message.error('上传视频大小不能超过30MB！');
                return false;
            }

            const isLt30 = file.name.length < 30;
            if (!isLt30) {
                _this.$message.error("上传视频文件名称长度必须要小于30个文字！");
                return false;
            }
        }

        if (_this.uploadForm.uploadType == 'audio') {
            //alert(file.type);
            if (['audio/mp3', 'audio/wav', 'audio/x-m4a', 'audio/ogg'].indexOf(file.type) == -1) {
                _this.$message.error('请上传正确的音频格式！');
                return false;
            }

            const isLt30M = file.size / 1024 / 1024 < 10;
            if (!isLt30M) {
                _this.$message.error('上传视频大小不能超过30MB哦！');
                return false;
            }

            const isLt30 = file.name.length < 30;
            if (!isLt30) {
                _this.$message.error("上传视频文件名称长度必须要小于30个文字！");
                return false;
            }
        }

    },

    //图片：上传成功后的回调
    handleUploadSuccess: function (response, file, fileList) {

        var _this = this;
        // console.log('上传文件=response=>', response)
        if (response.Status == "false") {

            alert(response.ErrMsg);

            //this.$message.error(response.errMsg);

        } else {

            file.url = response.ResultData.FileUrl;
            _this.uploadForm.fileList.push(file);

            _this.uploadForm.fileList = fileList;

            //_this.formData.fileUrl = response.resultData;

          let quill = this.$refs.myTextEditor.quill;
          let length = quill.getSelection().index; //获取当前鼠标焦点位置
          quill.insertEmbed(length, 'image', response.ResultData.FileUrl);//插入<img src='url'/> 
          quill.setSelection(length + 1)  //鼠标焦点位置

            //alert("上传成功");
          //_this.$message.success("上传成功");

        }

    },    
    //上传处理
    uploadOk(){

    }
  },    
}
</script>