<template>
  <div>

    <el-dialog
      title="备注"
      :visible.sync="dialogForm.dialogVisible"
      width="50%"
      :before-close="handleClose">
      <div style="height:700px">
        <editor @onEditorChange="onEditorChange" :value="dialogForm.content" style="height:640px"></editor>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveAnalysisContent" size="small">保 存</el-button>
        <el-button @click="dialogForm.dialogVisible = false" size="small">关 闭</el-button>
      </span>
    </el-dialog>

    <!-- 页面主体 -->
    <div class="boxmain">

      <!--检索条件-->
      <div>
        <div>
          <el-input v-model="formData.companyCode" placeholder="证券代码" size="mini" style="width:150px"></el-input>
          <el-select v-model="formData.agentType" placeholder="委托方向" size="mini" style="width:150px;margin-left:10px" @change="search()">
            <el-option
              v-for="item in formData.agentTypeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <el-input v-model="formData.tag" placeholder="标签 or" size="mini" style="width:150px;margin-left:10px"></el-input>      
          <el-button type="info" @click="search()" size="mini" style="margin-left:10px">检索</el-button>   
        </div>

        <div>
          <el-button type="info" @click="addRow()" size="mini" style="margin-top:10px">添加</el-button>   
        </div>
        
      </div>
    
      <!--统计信息-->
      <div style="margin-top:0px;">
        <div style="display:table;width:100%;border-collapse:separate; border-spacing:0px 10px;">
          <div style="display:table-row">
            <div style="display:table-cell">
                <span>共：{{total}}</span>
            </div>
          </div>
        </div>
      </div>

      <!--列表信息-->
      <div>

        <!--检索结果-->
        <el-table v-loading="loading"
            :data="viewData.gridData"
            show-summary
            :summary-method="getSummaries"
            style="font-size: 13px;width: 100%;margin-bottom: 20px;"
            :header-cell-style="({row, rowIndex})=>{return 'background-color:#F5F5F5;text-align:center;'}"
            :cell-style="({row, column, rowIndex, columnIndex})=>{return 'padding:5px 0px'}"
            @selection-change="handleSelectionChange">
            <el-table-column
              type="index"
              :index="indexMethod"
              width="40">
            </el-table-column>            
            <!-- <el-table-column type="selection"
                          label=""
                          width="30">
            </el-table-column> -->
            <!-- <el-table-column label="" prop="" width="50">
            </el-table-column> -->
            <!-- <el-table-column label="注" prop="" width="50" align="center">
              <template slot-scope="scope">
                <el-link icon="el-icon-edit"  type="primary" @click="viewDialog(scope.row)"></el-link>
                <span v-if="scope.row.Memo">*</span>
              </template>
            </el-table-column>   -->
            <el-table-column label="交易时间" prop="TradeTime" width="185" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-date-picker
                    v-model="rowForm.formData.TradeTime"
                    type="datetime"
                    value-format="yyyy-MM-dd HH:ss:mm"
                    size="mini"
                    style="width:175px">
                  </el-date-picker>
                </span>
                <span v-else>{{commonUtil.formatData(scope.row.TradeTime)}}</span>
              </template>                     
            </el-table-column>             
            <el-table-column label="证券代码" prop="CompanyCode" width="100" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.CompanyCode" placeholder="证券代码" size='mini'></el-input>
                </span>
                <span v-else>
                  <a :href="'http://quote.eastmoney.com/concept/' + (scope.row.TradeMkPlace.indexOf('深圳')>-1?'sz':'sh')  + scope.row.CompanyCode + '.html?from=classic'" target="_blank">{{scope.row.CompanyCode}}</a>
                </span>
              </template>               
            </el-table-column> 
            <el-table-column label="证券名称" prop="CompanyName" width="100" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.CompanyName" placeholder="证券名称" size='mini'></el-input>
                </span>
                <span v-else>
                  <el-link icon="el-icon-edit"  type="primary" @click="viewDialog(scope.row)" style="margin-left:5px"></el-link>
                  <span v-if="scope.row.Memo">*</span>                  
                  <span>{{scope.row.CompanyName}}</span>
                </span>                   
              </template>               
            </el-table-column>   
            <el-table-column label="委托方向" prop="AgentType" width="100" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.AgentType" placeholder="委托方向" size='mini'></el-input>
                </span>
                <span v-else>{{scope.row.AgentType}}</span>
              </template>  
            </el-table-column>    
            <el-table-column label="成交数量" prop="TradeVol" width="100" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.TradeVol" placeholder="成交数量" size='mini'></el-input>
                </span>
                <span v-else>{{scope.row.TradeVol}}</span>
              </template>  
            </el-table-column>         
            <el-table-column label="成交均价" prop="TradePriceAverage" width="90" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.TradePriceAverage" placeholder="成交均价" size='mini'></el-input>
                </span>
                <span v-else>{{scope.row.TradePriceAverage}}</span>
              </template>                  
            </el-table-column>             
            <el-table-column label="成交金额" prop="TradePrice" width="110" align="center" >
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.TradePrice" placeholder="成交金额" size='mini'></el-input>
                </span>
                <span v-else>{{scope.row.TradePrice}}</span>
              </template>                
            </el-table-column>  
            <el-table-column label="佣金" prop="Commission" width="60" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.Commission" placeholder="佣金" size='mini'></el-input>
                </span>
                <span v-else>{{scope.row.Commission}}</span>
              </template>               
            </el-table-column> 
            <el-table-column label="交易规费" prop="TradeFees" width="80" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.TradeFees" placeholder="交易规费" size='mini'></el-input>
                </span>
                <span v-else>{{scope.row.TradeFees}}</span>
              </template>               
            </el-table-column>             
            <el-table-column label="印花税" prop="StampTax" width="60" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.StampTax" placeholder="印花税" size='mini'></el-input>
                </span>
                <span v-else>{{scope.row.StampTax}}</span>
              </template>                 
            </el-table-column>    
            <el-table-column label="过户费" prop="TransferFees" width="60" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.TransferFees" placeholder="过户费" size='mini'></el-input>
                </span>
                <span v-else>{{scope.row.TransferFees}}</span>
              </template>                
            </el-table-column>                          
            <el-table-column label="交易市场" prop="TradeMkPlace" width="100" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.TradeMkPlace" placeholder="交易市场" size='mini'></el-input>
                </span>
                <span v-else>{{scope.row.TradeMkPlace}}</span>
              </template>                
            </el-table-column>  
            <el-table-column label="标签" prop="Tag" width="100" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input
                    type="textarea"
                    size="mini"
                    :rows="3"
                    placeholder="标签"
                    v-model="rowForm.formData.Tag">
                  </el-input>                  
                </span>
                <span v-else>{{scope.row.Tag}}</span>
              </template>                
            </el-table-column>              
            <el-table-column label="" prop="" width="150" align="center">>
               <template slot-scope="scope">
                <el-button v-if="rowForm.type == '0'" type="info" @click="editRow(scope.row)" size="mini">修改</el-button>
                <el-button v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id" type="info" @click="saveRow(scope.row)" size="mini">保存</el-button>
                <el-button v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id" type="info" @click="cancelRow()" size="mini">取消</el-button>
              </template>
            </el-table-column>                                                                                                   
                                  
        </el-table>

      </div>

    <!--页码-->
    <div style='margin-top:20px;text-align: center;'>
        <el-pagination background
          layout='total,sizes, prev, pager, next, jumper'
          :total='total'
          :page-sizes="[20,50,100,200]"
          :page-size='pagination.perPageNum'
          :current-page='pagination.curPage'
          @current-change='handleCurrentChange'
          @size-change="handleSizeChange">
        </el-pagination>
    </div>    
    
    </div>

  </div>

</template>
<script>
// import { mapMutations } from 'vuex';
import editor from '@/components/QuillEditor.vue';
export default {
  name: 'tradelog',
  data() {
    return {
      fn:'test',      
      posturl:'/TradeLog',
      loading: false,     

      viewData:{
        gridData:[],
        count: 0
      },

      formData:{
        multipleSelection:[],
        companyCode:'',
        agentTypeOptions: [{
                value: '',
                label: '委托方向(全部)'
            }, {
                value: '证券买入',
                label: '买入'                
            }, {
                value: '证券卖出',
                label: '卖出'
            }],
        agentType:"",
        tag:""
      },

      pagination: {
          perPageNum:20,//默认
          curPage: 1 //页码
      },

      dialogForm:{
        dialogVisible: false,
        id:"",
        content:""
      },

      rowForm:{
        type:'0', //0:显示模式 1:修改模式
 
        
      }          
     
    }
  },
  /* 这个是vue的钩子函数，当new Vue()实例创建完毕后执行的函数 */
  created () {   

    var _this = this;

    _this.search();

    
    console.log(_this.$store.state);

  },
  mounted(){
        // 绑定监听事件
    window.addEventListener('keydown', this.keyDown);        
  },  
  updated(){
  },
  components: { 
    editor
  },
  //计算属性
  computed: {
    UserId:function(){
      var userid = this.$store.state.UserInfo.UserId?this.$store.state.UserInfo.UserId:'';
      return userid;
    },

    //检索结果记录数
    total:function() {
        var _this = this;
        return Number(_this.viewData.count);
    }
  },
  destroyed() {
    // 销毁事件
    window.removeEventListener('keydown', this.keyDown, false);
  },  
  //方法
  methods: {

    //检索
    search() {

      var _this = this;

      // console.log(this.$store);

      try{
        
          _this.loading = true;

        var data = {
            UserId: _this.UserId,
            CurPage: _this.pagination.curPage,
            PerPageNum: _this.pagination.perPageNum,
            CompanyCode:_this.formData.companyCode,
            AgentType:_this.formData.agentType,
            Tag:_this.formData.tag
          }

        _this.$http.post(_this.posturl + "/search",data)
        .then(function (response) {

          if (response.data.Status == "false") {

            _this.$message.error(response.data.ErrMsg);

          } else {

            // console.log(response);

            var result = response.data.ResultData;

            _this.viewData.gridData = result.gridData;
            _this.viewData.count = result.count;

          }   

          _this.loading = false;
          
        })
        .catch(function (error) {

          console.log(error);

        }); 

      }catch(err){

        //_this.$message.error(err.message);
        alert(err.message);

      } 

    },

    //换页
    handleCurrentChange:function(pageNo) {
        var _this = this;
        _this.pagination.curPage = pageNo;
        _this.search();
    },    

    //更改size
    handleSizeChange:function(val) {
        var _this = this;
        _this.pagination.perPageNum = val;
        _this.search();
    },  
    
    //选择列表
    handleSelectionChange:function(val) {
        var _this = this;
        _this.formData.multipleSelection = val;
    },    

    //显示对话框
    viewDialog(row){
      var _this = this;

      _this.dialogForm.dialogVisible = true;

      _this.dialogForm.id = row.Id;
      _this.dialogForm.content = row.Memo;

      console.log(row.Memo);

    },

    //关闭对话框
    handleClose(done) {
      
      // this.$confirm('确认关闭？')
      //   .then(() => {
      //     done();
      //   })
      //   .catch(() => {});

      done();
      
    },

    onEditorChange(html) {

      var _this = this;
      
      _this.dialogForm.content = html;

      // console.log(_this.dialogForm.content);

    },

    //保存备注
    saveAnalysisContent(){

      var _this = this;

      try{
        
          console.log(_this.dialogForm.content);

          _this.loading = true;

          var data = {
              Id: _this.dialogForm.id,
              AnalysisContent: _this.dialogForm.content
            }

          _this.$http.post(_this.posturl + "/saveanalysiscontent",data)
          .then(function (response) {

            if (response.data.Status == "false") {

              _this.$message.error(response.data.ErrMsg);

            } else {

              // console.log(response);
              _this.$message.success("保存成功!");
              _this.search();
              // _this.dialogForm.dialogVisible = false;

            }   

            _this.loading = false;
            
          })
          .catch(function (error) {

            console.log(error);

          }); 

        }catch(err){

          //_this.$message.error(err.message);
          alert(err.message);

        } 

    },

    //表格索引
    indexMethod(index) {
        var _this = this;
        return (_this.pagination.curPage - 1) * _this.pagination.perPageNum + index + 1;
    },

    //编辑行
    editRow(row){
      
      var _this = this;

      _this.rowForm.type = 1;

      _this.rowForm.formData.Id = row.Id;
      _this.rowForm.formData.TradeTime = _this.commonUtil.formatData(row.TradeTime);
      _this.rowForm.formData.CompanyCode = row.CompanyCode;
      _this.rowForm.formData.CompanyName = row.CompanyName;
      _this.rowForm.formData.AgentType = row.AgentType;
      _this.rowForm.formData.TradeVol = row.TradeVol;
      _this.rowForm.formData.TradePriceAverage = row.TradePriceAverage;
      _this.rowForm.formData.TradePrice = row.TradePrice;
      _this.rowForm.formData.Commission = row.Commission;
      _this.rowForm.formData.TradeFees = row.TradeFees;
      _this.rowForm.formData.StampTax = row.StampTax;
      _this.rowForm.formData.TransferFees = row.TransferFees;
      _this.rowForm.formData.TradeMkPlace = row.TradeMkPlace;                        
      _this.rowForm.formData.Tag = row.Tag;
  
    },    
    //保存行
    saveRow(){

      var _this = this;

      try{
        
          _this.loading = true;

          var action = '/saverow';

          var data = _this.rowForm.formData;

          _this.$http.post(_this.posturl + action,data)
          .then(function (response) {

            if (response.data.Status == "false") {

              _this.$message.error(response.data.ErrMsg);

            } else {

              _this.rowForm.type = 0;
              _this.search();

            }   

            _this.loading = false;
            
          })
          .catch(function (error) {

            console.log(error);

          }); 

        }catch(err){

          //_this.$message.error(err.message);
          alert(err.message);

        } 

    },

    //添加行
    addRow(){

      var _this = this;

      if(!_this.viewData.gridData)
      {
        _this.viewData.gridData = [];
      } 

      _this.viewData.gridData.unshift({
      
          Id:'',
          TradeTime:'',
          CompanyCode:'',
          CompanyName:'',          
          AgentType:'',
          TradeVol:'0',
          TradePriceAverage:'0',
          TradePrice:'0',
          Commission:'0',
          TradeFees:'0',
          StampTax:'0',
          TransferFees:'0',
          TradeMkPlace:'',
      });
      
      _this.rowForm.type = 1;
      _this.rowForm.formData.Id = '';
      _this.rowForm.formData.UserId = _this.UserId;
      // _this.rowForm.formData.TradeTime = '';
      // _this.rowForm.formData.CompanyCode = '';
      // _this.rowForm.formData.CompanyName = '';
      // _this.rowForm.formData.AgentType = '';
      // _this.rowForm.formData.TradeVol = '';
      // _this.rowForm.formData.TradePrice = '';
      // _this.rowForm.formData.TradeMkPlace = '';                   
      // _this.rowForm.formData.Tag = '';
    },

    //编辑行-取消
    cancelRow(){
      var _this = this;
      _this.rowForm.type = 0;

      if(_this.rowForm.formData.Id == ''){
        _this.viewData.gridData.shift();
      }

    },

    //ctrl+s 保存
    keyDown(e) {

      if(!this.dialogForm.dialogVisible){
        return;
      }

      //ctrl+s 保存
      if (e.keyCode === 83 && e.ctrlKey) {

        this.saveAnalysisContent();

        event.preventDefault();

        event.returnValue = false;

        return false;          
      }

    },      

    //合计行
    getSummaries(param) {
       var _this = this;
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '计';
          return;
        }
        if(index != 5 && !(index >= 7 && index <= 11) ){
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          //sums[index] += ' 元';
          
          if(index==5){
            sums[index] = _this.commonUtil.formatNumFix(sums[index],0);   
          } else {
            sums[index] = _this.commonUtil.formatNumFix(sums[index],2);
            //sums[index] += ' 元';
          }
        } else {
          sums[index] = 'N/A';
        }
      });

      return sums;
    }
  

  }
}
</script>

<style scoped>

  .boxmain {
    width:100%;
    margin: 0px auto;
    text-align: left;
    font-size: 15px;
  }

</style>
