<template>
  <div>

    <el-dialog
      title="分析内容"
      :visible.sync="dialogForm.dialogVisible"
      width="50%"
      :before-close="handleClose"
      :close-on-click-modal ="false">
      <div slot="title" style="text-align: left;">
          <div>公司摘要</div>
          <hr style="height:1px;border:none;border-top:1px solid #E0E0E0;" />
      </div>       
      <div style="height:700px">
        <editor @onEditorChange="onEditorChange" :value="dialogForm.Description" style="height:640px"></editor>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveDescription" size="small">保 存</el-button>
        <el-button @click="dialogForm.dialogVisible = false" size="small">关 闭</el-button>
      </span>
    </el-dialog>

    <!-- 页面主体 -->
    <div class="boxmain">

      <!-- 检索条件     -->
      <div>

        <div>
          <el-input v-model="formData.CompanyCode" placeholder="证券代码" size="mini" style="width:150px"></el-input>
          <el-input v-model="formData.CompanyName" placeholder="证券名称 or" size="mini" style="width:150px;margin-left:10px"></el-input>
          <el-input v-model="formData.Tag" placeholder="概念/标签 and" size="mini" style="width:150px;margin-left:10px"></el-input> 
          <el-input v-model="formData.HoldCompanyName" placeholder="控股公司 or" size="mini" style="width:150px;margin-left:10px"></el-input>      
          <el-button type="info" @click="search()" v-on:keyup.enter="search()" size="mini" style="margin-left:10px">检索</el-button>   
        </div>

        <div>
          <el-button type="info" @click="addRow()" size="mini" style="margin-top:10px">添加</el-button>   
        </div>

      </div>
    
      <!--统计信息-->
      <div style="margin-top:0px;">
        <div style="display:table;width:100%;border-collapse:separate; border-spacing:0px 10px;">
          <div style="display:table-row">
            <div style="display:table-cell">
                <span>共：{{total}}</span>
            </div>
          </div>
        </div>
      </div>

      <!--列表信息-->
      <div>

        <!--检索结果-->
        <el-table v-loading="loading"
            :data="viewData.gridData"
            style="font-size: 13px;width: 100%;margin-bottom: 20px;"
            :header-cell-style="({row, rowIndex})=>{return 'background-color:#F5F5F5;text-align:center;'}"
            :cell-style="({row, column, rowIndex, columnIndex})=>{return 'padding:0px 0px'}"
            @selection-change="handleSelectionChange">
            <el-table-column
              type="index"
              :index="indexMethod"
              width="40">
            </el-table-column>            
            <!-- <el-table-column type="selection"
                          label=""
                          width="30">
            </el-table-column> -->
            <!-- <el-table-column label="注" prop="" width="50" align="center">
              <template slot-scope="scope">
                <el-link icon="el-icon-edit"  type="primary" @click="viewDialog(scope.row)"></el-link>
                <span v-if="scope.row.Description">*</span>
              </template>
            </el-table-column>   -->
            <!-- <el-table-column label="上市时间" prop="LaunchDate" width="90" align="center">
               <template slot-scope="scope">
                <span>{{commonUtil.formatData(scope.row.LaunchDate)}}</span>
              </template>
            </el-table-column>     -->
            <el-table-column label="证券代码" prop="CompanyCode" width="100" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.CompanyCode" placeholder="证券代码" size='mini'></el-input>
                </span>
                <span v-else>
                  <a :href="'http://quote.eastmoney.com/concept/' + (scope.row.CompanyType?scope.row.CompanyType:'sz') + scope.row.CompanyCode + '.html?from=classic'" target="_blank">{{scope.row.CompanyCode}}</a>
                </span>
              </template>               
            </el-table-column> 
            <el-table-column label="证券名称" prop="CompanyName" width="120" align="left">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.CompanyName" placeholder="证券名称" size='mini'></el-input>
                </span>
                <span v-else>
                  <el-link icon="el-icon-edit"  type="primary" @click="viewDialog(scope.row)" style="margin-left:5px"></el-link>
                  <span v-if="scope.row.Description">*</span>                  
                  <span>{{scope.row.CompanyName}}</span>
                </span>                   
              </template>                  
            </el-table-column>   
            <el-table-column label="证券类型" prop="CompanyType" width="80" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.CompanyType" placeholder="证券类型" size='mini'></el-input>
                </span>
                <span v-else>{{scope.row.CompanyType}}</span>
              </template>   
            </el-table-column> 
            <el-table-column label="控股公司" prop="HoldCompanyName" width="150" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.HoldCompanyName" placeholder="控股公司" size='mini'></el-input>
                </span>
                <span v-else>{{scope.row.HoldCompanyName}}</span>
              </template>              
            </el-table-column>  
            <el-table-column label="概念/标签" prop="Tag" width="350" align="left">
              <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input
                    type="textarea"
                    size="mini"
                    :rows="4"
                    placeholder="概念/标签"
                    v-model="rowForm.formData.Tag">
                  </el-input>
                </span>
                <span v-else>{{scope.row.Tag}}</span>                
              </template> 
            </el-table-column>  
            <el-table-column label="" prop="" align="center">>
               <template slot-scope="scope">
                <el-button v-if="rowForm.type == '0'" type="info" @click="editRow(scope.row)" size="mini">修改</el-button>
                <el-button v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id" type="info" @click="saveRow(scope.row)" size="mini">保存</el-button>
                <el-button v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id" type="info" @click="cancelRow()" size="mini">取消</el-button>
              </template>
            </el-table-column>
        </el-table>

      </div>

    <!--页码-->
    <div style='margin-top:20px;text-align: center;'>
        <el-pagination background
          layout='total,sizes, prev, pager, next, jumper'
          :total='total'
          :page-sizes="[20,50,100,200]"
          :page-size='pagination.perPageNum'
          :current-page='pagination.curPage'
          @current-change='handleCurrentChange'
          @size-change="handleSizeChange">
        </el-pagination>
    </div>    
    
    </div>

  </div>

</template>
<script>
import editor from '@/components/QuillEditor.vue';
export default {
  name: 'Company',
  data() {
    return {
      fn:'test',      
      posturl:'/Company',
      loading: false,     

      viewData:{
        gridData:[],
        count: 0
      },

      formData:{
        multipleSelection:[],
        CompanyCode:'',
        CompanyName:'',
        Tag:"",
        HoldCompanyName:''        
      },

      pagination: {
          perPageNum:20,//默认
          curPage: 1 //页码
      },

      dialogForm:{
        dialogVisible: false,
        Id:"",
        Description:""
      },

      rowForm:{
        type:'0', //0:显示模式 1:修改模式
        formData:{
          Id:"",
          CompanyCode:'',
          CompanyName:'',
          CompanyType:'',
          HoldCompanyName:'',
          Tag:''
        }
        
      }      
     
    }
  },
  /* 这个是vue的钩子函数，当new Vue()实例创建完毕后执行的函数 */
  created () {   

    var _this = this;

    _this.search();

  },
  mounted(){
        // 绑定监听事件
    window.addEventListener('keydown', this.keyDown);        
  },  
  updated(){
  },
  components: { 
    editor
  },
  //计算属性
  computed: {
    //检索结果记录数
    total:function() {
        var _this = this;
        return Number(_this.viewData.count);
    },
    UserId:function(){
      var userid = this.$store.state.UserInfo.UserId?this.$store.state.UserInfo.UserId:'';
      return userid;
    }       
  },
  destroyed() {
    // 销毁事件
    window.removeEventListener('keydown', this.keyDown, false);
  },   
  //方法
  methods: {

    //检索
    search() {

      var _this = this;

      try{
        
          _this.loading = true;

        var data = {
            CurPage: _this.pagination.curPage,
            PerPageNum: _this.pagination.perPageNum,
            CompanyCode:_this.formData.CompanyCode,
            CompanyName:_this.formData.CompanyName,
            Tag:_this.formData.Tag,
            HoldCompanyName:_this.formData.HoldCompanyName,
            UserId:_this.UserId
          }

        _this.$http.post(_this.posturl + "/search",data)
        .then(function (response) {

          if (response.data.Status == "false") {

            _this.$message.error(response.data.ErrMsg);

          } else {

            // console.log(response);

            var result = response.data.ResultData;

            _this.viewData.gridData = result.GridData;
            _this.viewData.count = result.Count;

          }   

          _this.loading = false;
          
        })
        .catch(function (error) {

          console.log(error);

        }); 

      }catch(err){

        //_this.$message.error(err.message);
        alert(err.message);

      } 

    },

    //换页
    handleCurrentChange:function(pageNo) {
        var _this = this;
        _this.pagination.curPage = pageNo;
        _this.search();
    },    

    //更改size
    handleSizeChange:function(val) {
        var _this = this;
        _this.pagination.perPageNum = val;
        _this.search();
    },  
    
    //选择列表
    handleSelectionChange:function(val) {
        var _this = this;
        _this.formData.multipleSelection = val;
    },    

    //显示对话框
    viewDialog(row){
      var _this = this;

      _this.dialogForm.Id = row.Id;
      _this.dialogForm.Description = row.Description;

      _this.dialogForm.dialogVisible = true;

    },

    //编辑行
    editRow(row){
      
      var _this = this;

      _this.rowForm.type = 1;

      _this.rowForm.formData.Id = row.Id;
      _this.rowForm.formData.CompanyCode = row.CompanyCode;
      _this.rowForm.formData.CompanyName = row.CompanyName;
      _this.rowForm.formData.CompanyType = row.CompanyType;
      _this.rowForm.formData.HoldCompanyName = row.HoldCompanyName;      
      _this.rowForm.formData.Tag = row.Tag;
  
    },

    //关闭对话框
    handleClose(done) {
      
      // this.$confirm('确认关闭？')
      //   .then(() => {
      //     done();
      //   })
      //   .catch(() => {});

      done();
      
    },

    onEditorChange(html) {

      var _this = this;
      
      _this.dialogForm.Description = html;

      // console.log(_this.dialogForm.content);

    },

    //保存分析内容:公司摘要
    saveDescription(){

      var _this = this;

      try{
        
          //console.log(_this.dialogForm.AnalysisContent);

          _this.loading = true;

          var data = {
              Id: _this.dialogForm.Id,
              Description: _this.dialogForm.Description
            }

          _this.$http.post(_this.posturl + "/savedescription",data)
          .then(function (response) {

            if (response.data.Status == "false") {

              _this.$message.error(response.data.ErrMsg);

            } else {

              // console.log(response);
              _this.$message.success("保存成功!");
              _this.search();
              // _this.dialogForm.dialogVisible = false;

            }   

            _this.loading = false;
            
          })
          .catch(function (error) {

            console.log(error);

          }); 

        }catch(err){

          //_this.$message.error(err.message);
          alert(err.message);

        } 

    },

    //表格索引
    indexMethod(index) {
        var _this = this;
        return (_this.pagination.curPage - 1) * _this.pagination.perPageNum + index + 1;
    },

    //修改
    saveRow(){

      var _this = this;

      try{
        
          _this.loading = true;

          var action = '/saverow';

          var data = _this.rowForm.formData;

          // data.UserId = _this.UserId;

          _this.$http.post(_this.posturl + action,data)
          .then(function (response) {

            if (response.data.Status == "false") {

              _this.$message.error(response.data.ErrMsg);

            } else {

              _this.rowForm.type = 0;
              _this.search();

            }   

            _this.loading = false;
            
          })
          .catch(function (error) {

            console.log(error);

          }); 

        }catch(err){

          //_this.$message.error(err.message);
          alert(err.message);

        } 

    },
    
    //添加行
    addRow(){

      var _this = this;

      if(!_this.viewData.gridData)
      {
        _this.viewData.gridData = [];
      } 

      _this.viewData.gridData.unshift({
      
          Id:'',
          CompanyCode:'',
          CompanyName:'',
          CompanyType:'',
          HoldCompanyName:'',            
          Tag:''

      });
      
      _this.rowForm.type = 1;
      _this.rowForm.formData.Id = '';
      _this.rowForm.formData.UserId = _this.UserId;

    },

    //编辑行-取消
    cancelRow(){
      var _this = this;
      _this.rowForm.type = 0;

      if(_this.rowForm.formData.Id == ''){
        _this.viewData.gridData.shift();
      }

    },
    
    //ctrl+s 保存
    keyDown(e) {

      if(!this.dialogForm.dialogVisible){
        return;
      }

      //ctrl+s 保存
      if (e.keyCode === 83 && e.ctrlKey) {

        this.saveDescription();

        event.preventDefault();

        event.returnValue = false;

        return false;          
      }

    },    

  }
}
</script>

<style scoped>

  .boxmain {
    width:100%;
    margin: 0px auto;
    text-align: left;
    font-size: 15px;
  }

  .el-dialog__body {
      padding: 10px 20px 10px 20px;
      color: #606266;
      font-size: 14px;
      word-break: break-all;
  }

</style>
