import Vue from 'vue'
import App from './App.vue'
import store from './store'
import router from './router'

//通用Js S
import commonUtil from './commonjs/common.js';
Vue.prototype.commonUtil = commonUtil //绑定axios->Vue，可在子组件使用
//通用Js E

//第三方插件 Start

import axios from 'axios'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

Vue.use(ElementUI);
Vue.prototype.$http = axios //绑定axios->Vue，可在子组件使用

// 添加请求拦截器，在请求头中加token
axios.interceptors.request.use(
  config => {
    if (localStorage.getItem('Authorization')) {
      config.headers.Authorization = localStorage.getItem('Authorization');
    }
 
    return config;
  },
  error => {
    return Promise.reject(error)
  });

  //axios.defaults.baseURL = 'http://www.rlf99.com';
  //axios.defaults.baseURL = 'http://localhost:49427';
  // axios.defaults.baseURL = process.env.NODE_ENV === 'development'?'http://localhost:49427':'http://www.rlf99.com'

  // 添加响应拦截器，处理响应错误
  axios.interceptors.response.use(function (response) {
    
    //处理响应数据
    return response;

  }, function (error) {

    // 处理响应错误
    if (error.response.status === 401) {
        // const data = {
        //     code:-1,
        //     message:'登录超时'
        // }
        // return Promise.reject(data);

        //console.log(error);

        alert("登录已过期!请重新登录!");

        //认证失败,跳转到登录页
        localStorage.removeItem('Authorization');      
        router.push('/login');        
    }
    return Promise.reject(error);
  });

//第三方插件 End

Vue.config.productionTip = false

new Vue({
  store,
  router,
  render: h => h(App)
}).$mount('#app')
