<template>
  <div v-loading="loading">
    <el-form ref="userForm" :model="userForm" :rules="userForm.rules" label-width="80px">
      <h3 class="login-title">个人设置</h3>
      <el-form-item label="账号" prop="username">
        <el-input type="text" placeholder="请输入账号" v-model="userForm.username" style="width:100px"/>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input type="password" placeholder="请输入密码" v-model="userForm.password"/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" v-on:click="save('loginForm')">保存</el-button>
      </el-form-item>

    </el-form>

  </div>
</template>
<script>

// import qs from 'qs';

export default {
  name: 'userset',
  data() {
    return {
      fn:'userset',      
      posturl:'/UserSet',
      loading: false,      

      userForm:{
        username:'',
        password:'',

        // 表单验证，需要在 el-form-item 元素中增加 prop 属性
        rules: {
          username: [
            {required: true, message: '账号不可为空', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '密码不可为空', trigger: 'blur'}
          ]
        },

      },

      // views:{
      //   token:''
      // }
      // dialogForm:{
      //   dialogVisible:false
      // }
      
    }
  },
  /* 这个是vue的钩子函数，当new Vue()实例创建完毕后执行的函数 */
  created () {   
  },
  mounted(){
  },  
  updated(){
  },
  components: { 
  },
  //计算属性
  computed: {
    UserId:function(){
      var userid = this.$store.state.UserInfo.UserId?this.$store.state.UserInfo.UserId:'';
      return userid;
    }          
  },
  //方法
  methods: {
      //登录处理
      login(formName) {

        var _this = this;

        try{
          
          var isValid = true;

          // 为表单绑定验证功能
          _this.$refs[formName].validate((valid) => {
            if (!valid) {
            //   // 使用 vue-router 路由到指定页面，该方式称之为编程式导航
            //   this.$router.push("/main");
            // } else {
              _this.$message.warning("请输入用户名和密码!");
            // this.$message({
            //   message: '警告哦，这是一条警告消息',
            //   type: 'warning'
            // });              
              //return false;
              isValid = false;
            }
          });

          if(!isValid){
            return;
          }

          _this.loading = true;
          var data = {
                username: _this.userForm.username,
                password:_this.userForm.password
              }

          // _this.$http.defaults.headers.post['Content-Type'] = 'application/json';
          
          _this.$http.post(_this.posturl,data)
          .then(function (response) {
            
              if (response.data.Status == "false") {

                _this.$message.error(response.data.ErrMsg);

              } else {

                console.log(response.data);
                // _this.views.token = response.data;

                var result = response.data.ResultData;

                // 将用户token保存到vuex中
                var userToken = 'Bearer ' + result.Token;
                // _this.changeLogin({ Authorization: userToken,UserName:_this.loginForm.username });

                var userid = result.UserId;
                // var userInfo = {UserId:userid,UserName:_this.loginForm.username} ;
                _this.changeLogin({ Authorization: userToken,UserInfo:{UserId:userid,UserName:_this.loginForm.username} });

                _this.$message.success("登录成功!");

                _this.$router.push({path:'/stockart'});

              }   

              _this.loading = false;              
             
          })
          .catch(function (error) {
            //alert(error);
            _this.$message.error(error);
          }); 

        }catch(err){

          //alert(err.message);
          _this.$message.error(err.message);

        } 

      }      

  },
}
</script>

<style scoped>
  .login-box {
    border: 1px solid #DCDFE6;
    width: 350px;
    margin: 180px auto;
    padding: 35px 35px 15px 35px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    box-shadow: 0 0 25px #909399;
  }

  .login-title {
    text-align: center;
    margin: 0 auto 40px auto;
    color: #303133;
  }
</style>
