import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {

    // 存储token
    Authorization: localStorage.getItem('Authorization') ? localStorage.getItem('Authorization') : '',
    UserInfo: {UserName:localStorage.getItem('UserName') ? localStorage.getItem('UserName') : '',
    UserId:localStorage.getItem('UserId') ? localStorage.getItem('UserId') : ''}

  },
  mutations: {

    //token,登录用户名存入localStorage
    changeLogin (state, user) {

      // console.log(user);
      state.Authorization = user.Authorization;

      state.UserInfo = user.UserInfo;

      // console.log(state.UserInfo);

      localStorage.setItem('Authorization', user.Authorization);
      localStorage.setItem('UserId', user.UserInfo.UserId);
      localStorage.setItem('UserName', user.UserInfo.UserName);

    }
 
  },
  actions: {
  },
  modules: {
  }
})
