<template>
  <div>

    <el-dialog
      title="分析内容"
      :visible.sync="dialogForm.dialogVisible"
      width="50%"
      :before-close="handleClose"
      :close-on-click-modal ="false">
      <div style="height:700px">
        <editor @onEditorChange="onEditorChange" :value="dialogForm.AnalysisContent" style="height:640px"></editor>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveAnalysisContent" size="small">保 存</el-button>
        <el-button @click="dialogForm.dialogVisible = false" size="small">关 闭</el-button>
      </span>
    </el-dialog>

    <!-- 页面主体 -->
    <div class="boxmain">

      <!-- 检索条件     -->
      <div>

        <div>
          <el-input v-model="formData.CompanyCode" placeholder="证券代码" size="mini" style="width:150px"></el-input>
          <el-input v-model="formData.CompanyName" placeholder="证券名称 or" size="mini" style="width:150px;margin-left:10px"></el-input>  
          <el-input v-model="formData.Tag" placeholder="标签 and" size="mini" style="width:150px;margin-left:10px"></el-input>  
          <el-button type="info" @click="search()" size="mini" style="margin-left:10px">检索</el-button>   
        </div>

        <div>
          <el-button type="info" @click="addRow()" size="mini" style="margin-top:10px">添加</el-button>   
        </div>

      </div>
    
      <!--统计信息-->
      <div style="margin-top:0px;">
        <div style="display:table;width:100%;border-collapse:separate; border-spacing:0px 10px;">
          <div style="display:table-row">
            <div style="display:table-cell">
                <span>共：{{total}}</span>
            </div>
          </div>
        </div>
      </div>

      <!--列表信息-->
      <div>

        <!--检索结果-->
        <el-table v-loading="loading"
            :data="viewData.gridData"
            style="font-size: 13px;width: 100%;margin-bottom: 20px;"
            :header-cell-style="({row, rowIndex})=>{return 'background-color:#F5F5F5;text-align:center;'}"
            :cell-style="({row, column, rowIndex, columnIndex})=>{return 'padding:5px 0px'}"
            show-summary
            :summary-method="getSummaries"            
            @selection-change="handleSelectionChange">
            <el-table-column
              type="index"
              :index="indexMethod"
              width="40">
            </el-table-column>            
            <!-- <el-table-column type="selection"
                          label=""
                          width="30">
            </el-table-column> -->
            <el-table-column label="建仓时间" prop="PositionStartTime" width="195" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-date-picker
                    v-model="rowForm.formData.PositionStartTime"
                    style="width:175px"
                    type="datetime"
                    value-format="yyyy-MM-dd HH:ss:mm"
                    size="mini">
                  </el-date-picker>             
                </span>
                <span v-else>{{commonUtil.formatData(scope.row.PositionStartTime)}}</span>
              </template>  
            </el-table-column>    
            <el-table-column label="清仓时间" prop="PositionEndTime" width="195" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-date-picker
                    v-model="rowForm.formData.PositionEndTime"
                    style="width:175px"
                    type="datetime"
                    value-format="yyyy-MM-dd HH:ss:mm"
                    size="mini">
                  </el-date-picker>             
                </span>
                <span v-else>{{commonUtil.formatData(scope.row.PositionEndTime)}}</span>
              </template>                             
            </el-table-column>                           
            <el-table-column label="证券代码" prop="CompanyCode" width="100" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.CompanyCode" placeholder="证券代码" size='mini'></el-input>                 
                </span>
                <span v-else>
                  <a :href="'http://quote.eastmoney.com/concept/' + (scope.row.TradeMkPlace.indexOf('深圳')>-1?'sz':'sh')  + scope.row.CompanyCode + '.html?from=classic'" target="_blank">{{scope.row.CompanyCode}}</a>
                </span>
              </template>                  
            </el-table-column> 
            <el-table-column label="证券名称" prop="CompanyName" width="100" align="left">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.CompanyName" placeholder="证券名称" size='mini'></el-input>
                </span>
                <span v-else>
                  <el-link icon="el-icon-edit"  type="primary" @click="viewDialog(scope.row)" style="margin-left:5px"></el-link>
                  <span v-if="scope.row.AnalysisContent">*</span>                  
                  <span>{{scope.row.CompanyName}}</span>
                </span>                   
              </template>              
            </el-table-column>   
            <el-table-column label="持仓数量" prop="PositionVol" width="90" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.PositionVol" placeholder="持仓数量" size='mini'></el-input>                 
                </span>
                <span v-else>{{scope.row.PositionVol}}</span>
              </template>               
            </el-table-column> 
            <el-table-column label="成本价格" prop="CostPrice" width="90" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.CostPrice" placeholder="成本价格" size='mini'></el-input>                 
                </span>
                <span v-else>{{scope.row.CostPrice}}</span>
              </template>               
            </el-table-column>  
            <el-table-column label="当前价格" prop="CurrentPrice" width="90" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.CurrentPrice" placeholder="当前价格" size='mini'></el-input>                 
                </span>
                <span v-else>{{scope.row.CurrentPrice}}</span>
              </template> 
            </el-table-column>  
            <el-table-column label="盈亏比例" prop="" width="80" align="center">
               <template slot-scope="scope">
                <span>{{commonUtil.formatNumFix(((scope.row.CurrentPrice-scope.row.CostPrice)/scope.row.CostPrice)*100,3)}}%</span>
              </template>                
            </el-table-column>  
            <el-table-column label="盈亏" prop="" width="80" align="center">
               <template slot-scope="scope">
                <span>{{commonUtil.formatNumFix((scope.row.CurrentPrice*scope.row.PositionVol-scope.row.CostPrice*scope.row.PositionVol),0)}}</span>
              </template>                  
            </el-table-column>  
            <el-table-column label="当前市值" prop="" width="80" align="center">
               <template slot-scope="scope">
                <span>{{commonUtil.formatNumFix((scope.row.CurrentPrice*scope.row.PositionVol),2)}}</span>
              </template>                  
            </el-table-column>                                      
            <el-table-column label="交易市场" prop="TradeMkPlace" width="100" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.TradeMkPlace" placeholder="交易市场" size='mini'></el-input>                 
                </span>
                <span v-else>{{scope.row.TradeMkPlace}}</span>
              </template>                   
            </el-table-column>    
            <el-table-column label="标签" prop="Tag" width="100" align="center">
             <template slot-scope="scope">
                <span v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id">
                  <el-input v-model="rowForm.formData.Tag" placeholder="标签" size='mini'></el-input>                 
                </span>
                <span v-else>{{scope.row.Tag}}</span>
              </template>                
            </el-table-column>                                                                                                    
            <el-table-column label="" prop="">
               <template slot-scope="scope">
                <el-button v-if="rowForm.type == '0'" type="info" @click="editRow(scope.row)" size="mini">修改</el-button>
                <el-button v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id" type="info" @click="saveRow(scope.row)" size="mini">保存</el-button>
                <el-button v-if="rowForm.type == '1' && rowForm.formData.Id == scope.row.Id" type="info" @click="cancelRow()" size="mini">取消</el-button> 
              </template>
            </el-table-column>                                    
        </el-table>

      </div>

    <!--页码-->
    <div style='margin-top:20px;text-align: center;'>
        <el-pagination background
          layout='total,sizes, prev, pager, next, jumper'
          :total='total'
          :page-sizes="[20,50,100,200]"
          :page-size='pagination.perPageNum'
          :current-page='pagination.curPage'
          @current-change='handleCurrentChange'
          @size-change="handleSizeChange">
        </el-pagination>
    </div>    
    
    </div>

  </div>

</template>
<script>
import editor from '@/components/QuillEditor.vue';
export default {
  name: 'PositionAnalysis',
  data() {
    return {
      fn:'test',      
      posturl:'/PositionAnalysis',
      loading: false,     

      viewData:{
        gridData:[],
        count: 0
      },

      formData:{
        multipleSelection:[],
        CompanyCode:'',
        CompanyName:'',
        Tag:'持仓'      
      },

      pagination: {
          perPageNum:20,//默认
          curPage: 1 //页码
      },

      dialogForm:{
        dialogVisible: false,
        Id:"",
        AnalysisContent:""
      },

      rowForm:{
        type:'0', //0:显示模式 1:编辑模式
        formData:{
          Id:"",
          PositionStartTime:'',
          PositionEndTime:'',
          CompanyCode:'',
          CompanyName:'',
          PositionVol:'',
          CostPrice:'',
          CurrentPrice:'',
          TradeMkPlace:'',
          Tag:''
        }        
       
      }      
     
    }
  },
  /* 这个是vue的钩子函数，当new Vue()实例创建完毕后执行的函数 */
  created () {   

    var _this = this;

    _this.search();

  },
  mounted(){
        // 绑定监听事件
    window.addEventListener('keydown', this.keyDown);    
  },  
  updated(){
  },
  components: { 
    editor
  },
  //计算属性
  computed: {
    //检索结果记录数
    total:function() {
        var _this = this;
        return Number(_this.viewData.count);
    },
    UserId:function(){
      var userid = this.$store.state.UserInfo.UserId?this.$store.state.UserInfo.UserId:'';
      return userid;
    }       
  },
  destroyed() {
    // 销毁事件
    window.removeEventListener('keydown', this.keyDown, false);
  },    
  //方法
  methods: {

    //检索
    search() {

      var _this = this;

      try{

        console.log("UserId=" + _this.UserId);
        
          _this.loading = true;

        var data = {
            CurPage: _this.pagination.curPage,
            PerPageNum: _this.pagination.perPageNum,
            CompanyCode:_this.formData.CompanyCode,
            CompanyName:_this.formData.CompanyName,
            Tag:_this.formData.Tag,
            UserId:_this.UserId
          }

        _this.$http.post(_this.posturl + "/search",data)
        .then(function (response) {

          if (response.data.Status == "false") {

            _this.$message.error(response.data.ErrMsg);

          } else {

            // console.log(response);

            var result = response.data.ResultData;

            _this.viewData.gridData = result.GridData;
            _this.viewData.count = result.Count;

          }   

          _this.loading = false;
          
        })
        .catch(function (error) {

          console.log(error);

        }); 

      }catch(err){

        //_this.$message.error(err.message);
        alert(err.message);

      } 

    },

    //换页
    handleCurrentChange:function(pageNo) {
        var _this = this;
        _this.pagination.curPage = pageNo;
        _this.search();
    },    

    //更改size
    handleSizeChange:function(val) {
        var _this = this;
        _this.pagination.perPageNum = val;
        _this.search();
    },  
    
    //选择列表
    handleSelectionChange:function(val) {
        var _this = this;
        _this.formData.multipleSelection = val;
    },    

    //显示对话框
    viewDialog(row){
      var _this = this;

      _this.dialogForm.Id = row.Id;
      _this.dialogForm.AnalysisContent = row.AnalysisContent;

      //console.log(row.AnalysisContent);

      _this.dialogForm.dialogVisible = true;

    },

    //关闭对话框
    handleClose(done) {
      
      // this.$confirm('确认关闭？')
      //   .then(() => {
      //     done();
      //   })
      //   .catch(() => {});

      done();
      
    },

    onEditorChange(html) {

      var _this = this;
      
      _this.dialogForm.AnalysisContent = html;

      // console.log(_this.dialogForm.content);

    },

    //保存分析内容
    saveAnalysisContent(){

      var _this = this;

      try{
        
          //console.log(_this.dialogForm.AnalysisContent);

          _this.loading = true;

          var data = {
              Id: _this.dialogForm.Id,
              AnalysisContent: _this.dialogForm.AnalysisContent
            }

          _this.$http.post(_this.posturl + "/saveanlysiscontent",data)
          .then(function (response) {

            if (response.data.Status == "false") {

              _this.$message.error(response.data.ErrMsg);

            } else {

              // console.log(response);
              _this.$message.success("保存成功!");
              _this.search();
              // _this.dialogForm.dialogVisible = false;

            }   

            _this.loading = false;
            
          })
          .catch(function (error) {

            console.log(error);

          }); 

        }catch(err){

          //_this.$message.error(err.message);
          alert(err.message);

        } 

    },

    //表格索引
    indexMethod(index) {
        var _this = this;
        return (_this.pagination.curPage - 1) * _this.pagination.perPageNum + index + 1;
    },

    //编辑行
    editRow(row){
      
      var _this = this;

      _this.rowForm.type = 1;

      _this.rowForm.formData.Id = row.Id;
      _this.rowForm.formData.PositionStartTime = _this.commonUtil.formatData(row.PositionStartTime);
      _this.rowForm.formData.PositionEndTime = _this.commonUtil.formatData(row.PositionEndTime);
      _this.rowForm.formData.CompanyCode = row.CompanyCode;
      _this.rowForm.formData.CompanyName = row.CompanyName;
      _this.rowForm.formData.PositionVol = row.PositionVol;
      _this.rowForm.formData.CostPrice = row.CostPrice;
      _this.rowForm.formData.CurrentPrice = row.CurrentPrice;
      _this.rowForm.formData.TradeMkPlace = row.TradeMkPlace;  
      _this.rowForm.formData.Tag = row.Tag;  
  
    },     

    //保存行
    saveRow(){

      var _this = this;

      try{
        
          _this.loading = true;

          var action = '/saverow';

          var data = _this.rowForm.formData;
          // data.UserId = _this.UserId;

          _this.$http.post(_this.posturl + action,data)
          .then(function (response) {

            if (response.data.Status == "false") {

              _this.$message.error(response.data.ErrMsg);

            } else {

              _this.rowForm.type = 0;
              _this.search();

            }   

            _this.loading = false;
            
          })
          .catch(function (error) {

            console.log(error);

          }); 

        }catch(err){

          //_this.$message.error(err.message);
          alert(err.message);

        } 

    },

    //添加行
    addRow(){

      var _this = this;

      if(!_this.viewData.gridData)
      {
        _this.viewData.gridData = [];
      } 

      _this.viewData.gridData.unshift({
      
          Id:'',
          PositionStartTime:'',
          PositionEndTime:'',
          CompanyCode:'',
          CompanyName:'',
          PositionVol:'',
          CostPrice:'',
          CurrentPrice:'',
          TradeMkPlace:'',
          Tag:''

      });
      
      _this.rowForm.type = 1;
      _this.rowForm.formData.Id = '';
      _this.rowForm.formData.UserId = _this.UserId;

    },

    //编辑行-取消
    cancelRow(){
      var _this = this;
      _this.rowForm.type = 0;

      if(_this.rowForm.formData.Id == ''){
        _this.viewData.gridData.shift();
      }

    },

    //ctrl+s 保存
    keyDown(e) {

      if(!this.dialogForm.dialogVisible){
        return;
      }

      //ctrl+s 保存
      if (e.keyCode === 83 && e.ctrlKey) {

        this.saveAnalysisContent();

        event.preventDefault();

        event.returnValue = false;

        return false;          
      }

    },    

    //合计行
    getSummaries(param) {

      // console.log(param);

      // var _this = this;
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '计';
          return;
        }
        if(!(index == 5 || index == 9 || index == 10)){
          return;
        }
        // if(index != 9){
        //   return;
        // }

        //const values = data.map(item => Number(item[column.property]));
        var values = [];

        if(index == 9){
          values = data.map(item => {

            return Number(item['CurrentPrice']*item['PositionVol']-item['CostPrice']*item['PositionVol']);

          });
        } else if(index == 10){
          values = data.map(item => {

            return Number(item['CurrentPrice']*item['PositionVol']);

          });
        } else {
          values = data.map(item => Number(item[column.property]));
        }

        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);

          //sums[index] += ' 元';
          
          if(index==9 || index==10){
            sums[index] = sums[index].toFixed(0);
          }

        } else {
          sums[index] = 'N/A';
        }
      });

      return sums;
    }    

  }
}
</script>

<style scoped>

  .boxmain {
    width:100%;
    margin: 0px auto;
    text-align: left;
    font-size: 15px;
  }

  /* .el-date-editor.el-input{
    width:175px
  } */

</style>
