<template>
  <div v-loading="loading">
    <el-form ref="loginForm" :model="loginForm" :rules="loginForm.rules" label-width="80px" class="login-box">
      <h3 class="login-title">欢迎登录</h3>
      <el-form-item label="账号" prop="username">
        <el-input type="text" placeholder="请输入账号" v-model="loginForm.username"/>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input type="password" placeholder="请输入密码" v-model="loginForm.password"/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" v-on:click="login('loginForm')">登录</el-button>
      </el-form-item>

      <!-- <div style="width:350px;word-break: break-all;word-wrap: break-word;"> 
        token:{{views.token}}   

      </div> -->

    </el-form>

    <!-- <el-dialog
      title="温馨提示"
      :visible.sync="dialogForm.dialogVisible"
      width="30%"
      :before-close="handleClose">
      <span>请输入账号和密码</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog> -->
  </div>
</template>
<script>

// import qs from 'qs';

import { mapMutations } from 'vuex';

export default {
  name: 'login',
  data() {
    return {
      fn:'login',      
      //posturl:'http://www.rlf99.com/api/Authentication/requestToken',
      posturl:'/api/Authentication/requestToken1',
      loading: false,      

      loginForm:{
        username:'',
        password:'',

        // 表单验证，需要在 el-form-item 元素中增加 prop 属性
        rules: {
          username: [
            {required: true, message: '账号不可为空', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '密码不可为空', trigger: 'blur'}
          ]
        },

      },

      // views:{
      //   token:''
      // }
      // dialogForm:{
      //   dialogVisible:false
      // }
      
    }
  },
  /* 这个是vue的钩子函数，当new Vue()实例创建完毕后执行的函数 */
  created () {   
  },
  mounted(){
    // 绑定监听事件
    window.addEventListener('keydown', this.keyDown);    
  },  
  updated(){
  },
  components: { 
  },
  //计算属性
  computed: {
  },
  destroyed() {
    // 销毁事件
    window.removeEventListener('keydown', this.keyDown, false);
  },  
  //方法
  methods: {
      ...mapMutations(['changeLogin']),
      //登录处理
      login(formName) {

        var _this = this;

        try{
          
          var isValid = true;

          // 为表单绑定验证功能
          _this.$refs[formName].validate((valid) => {
            if (!valid) {
            //   // 使用 vue-router 路由到指定页面，该方式称之为编程式导航
            //   this.$router.push("/main");
            // } else {
              _this.$message.warning("请输入用户名和密码!");
            // this.$message({
            //   message: '警告哦，这是一条警告消息',
            //   type: 'warning'
            // });              
              //return false;
              isValid = false;
            }
          });

          if(!isValid){
            return;
          }

          _this.loading = true;
          var data = {
                username: _this.loginForm.username,
                password:_this.loginForm.password
              }

          // _this.$http.defaults.headers.post['Content-Type'] = 'application/json';
          
          _this.$http.post(_this.posturl,data)
          .then(function (response) {
            
              if (response.data.Status == "false") {

                _this.$message.error(response.data.ErrMsg);

              } else {

                console.log(response.data);
                // _this.views.token = response.data;

                var result = response.data.ResultData;

                // 将用户token保存到vuex中
                var userToken = 'Bearer ' + result.Token;
                // _this.changeLogin({ Authorization: userToken,UserName:_this.loginForm.username });

                var userid = result.UserId;
                // var userInfo = {UserId:userid,UserName:_this.loginForm.username} ;
                _this.changeLogin({ Authorization: userToken,UserInfo:{UserId:userid,UserName:_this.loginForm.username} });

                _this.$message.success("登录成功!");

                _this.$router.push({path:'/stockart'});

              }   

              _this.loading = false;              
             
          })
          .catch(function (error) {
            //alert(error);
            _this.$message.error(error);
          }); 

        }catch(err){

          //alert(err.message);
          _this.$message.error(err.message);

        } 

      },

      keyDown(e) {
      // 回车执行登录 enter键的ASCII是13
          if (e.keyCode === 13) {
              this.login('loginForm'); // 登录 
          }
      }            

  },
}
</script>

<style scoped>
  .login-box {
    border: 1px solid #DCDFE6;
    width: 350px;
    margin: 180px auto;
    padding: 35px 35px 15px 35px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    box-shadow: 0 0 25px #909399;
  }

  .login-title {
    text-align: center;
    margin: 0 auto 40px auto;
    color: #303133;
  }
</style>
