    
// 共通类
let commonUtil = {
 
};

//格式化日期去T
commonUtil.formatData = function (date) {
    if(!date){
        return '';
    }
    return date.replace("T", " ");
}

//保留n位小数,四舍五入
commonUtil.formatNumFix=function(num,fix){
    // let numStr = num.toString()
    // let index = numStr.indexOf('.')
    // let result = numStr.slice(0, index + fix)    
    let result = num.toFixed(fix);
    return result;
}    
 
export default commonUtil;