import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import TradeLog from '../views/TradeLog.vue'
import PositionAnalysis from '../views/PositionAnalysis.vue'
import Company from '../views/Company.vue'
import StockArt from '../views/StockArt.vue'
import UserSet from '../views/UserSet.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    // 登录页
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    // 交易记录
    path: '/tradelog',
    name: 'TradeLog',
    component: TradeLog
  },
  {
    // 持仓分析
    path: '/positionanalysis',
    name: 'PositionAnalysis',
    component: PositionAnalysis
  },
  {
    // 持仓分析
    path: '/company',
    name: 'Company',
    component: Company
  },
  {
    // 证券文章
    path: '/stockart',
    name: 'StockArt',
    component: StockArt
  },
  {
    // 个人设置
    path: '/userset',
    name: 'UserSet',
    component: UserSet
  }            

]

const router = new VueRouter({
  routes
})

// 导航守卫
// 使用 router.beforeEach 注册一个全局前置守卫，判断用户是否登陆
router.beforeEach((to, from, next) => {

  // console.log(to.path);

  if (to.path === '/login') {
    next();
  } else {
    let token = localStorage.getItem('Authorization');
 
    if (token === null || token === '') {
      next('/login');
    } else {
      next();
    }
  }
});

export default router
